.night-status,.night-new-status{
	background: #000 url(../../img/wx/laikan/chapter-night-new-bg.jpg);
	color: #999;
	.txtbox-item h3{
		color: #666;
	}
	.demo .font-change a{
		border: 1px solid #999;
	}
	.intro .chapter{
		border-right: 1px solid #ccc;
		color: #999;
		i{
			border: 2px solid #eee;
			&.red{
				background: #ccc;
			}
		}
	}
	.font_list{
		border-top: solid 1px #ccc;
	}
	.alert-c{
		color: #999;
	}
	.intro div{
		color: #999;
	}
	.blue{
		color: #ccc!important;
		display: inline-block;
		font-style: normal;
	}
	.chapter-time{
		border-bottom:solid 1px #333;color:#666;
	}
	.chapter-mod-div{
		border-top:solid 1px #333;
	}
	.chapter_spread li a{
		color: #999;
		span{
			color: #999;
		}
	}
	.chapter-mod-list li a{
		color: #6cd;
		border: 1px solid #6cd;
		&.first{
			border: 1px solid #999;
			background: #666;
		}
	}
	.chapter_ma {
		border-bottom: solid 1px #333;
		span{
			background: #222;
			color: #999;
		}
		p{
			color: #999;
		}
	}
	.chapter-shelf{
		color: #999;
		a{
			color: #999;
		}
	}
	#fontBulb a{
		color:#6cd;
	}
	.chapter_ma_tit{
		border-bottom:solid 1px #333;
	}
	.chapter-collect{
		float:left;
		margin:1rem 0rem .5rem 1.5rem;
		a{
		    padding:2px 8px 3px;
		    display: inline-block;
		    font-size:1.2rem;
		    border-radius: 3px;
		    border: 1px solid #999;
		    color:#999;
		}
	}
	.chapter-app-down{
		border-bottom:solid 1px #333;
		a{
			color:#2be;
			background:none;
		}
	}
	.chapter_spread li a p.r{
		padding-bottom:1rem;
		border-bottom:solid 1px #333;
	}
}
.night-new-status{
	background: #000 url(../../img/wx/laikan/chapter_spread_night_bg.jpg);
	.chapter-app-down{
		border-bottom:solid 1px #999;
	}
	.chapter_spread li a p.r{
		border-bottom:solid 1px #999;
	}
	.txtbox-item h3{
		color: #aaa;
	}
	.chapter-mod-div{
		border-top:none;
		margin-top:0rem;
	}
	.chapter-app-down {
		.chapter-app-text{
			color:#999;
			background:#444;
		}
		a{
			border:solid 1px #2be;
		}
		
	}
	.chapter-mod-list li a{
	    color: #2be;
    	border: 1px solid #2be;
	}
	.collent-dialog{
		.chapter-spread-ma{
			border:none;
		}
	}
}

